@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* base styles */
body {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    font-size: 1.5em;
    text-align: center;
    background-color: #1a1a1c;
    color: #fff;
    overflow-x: hidden;
}

.cosmetic-card{
    position: relative;
    width: 200px;
    height: 300px;
    border-style: solid;
    overflow-y: hidden;
    overflow-x: hidden;
  }
  
  .cosmetic-card:hover{
    -webkit-transform: scale(1.03, 1.03);
            transform: scale(1.03, 1.03);
    transition: all ease-in 0.3s;
  }
  
  .img-body{
    width: 200px;
    height: 200px;
    overflow-y: hidden;
  }
  
  img{
    width: 100%;
    display: block;
  }
  
  .cosmetic-body{
    height: 50px;
    font-size: 8px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .cosmetic-title{
    font-size: 16px;
    font-weight: bold;
    margin: 10px 0;
  }
  
  .cosmetic-small-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .brand{
    font-size: 15px;
    margin-top: -3px;
  }
  
  .price{
    font-weight: bolder;
    font-size: 18px;
    margin-top: -20px;
  }
  
  
  /* mobile */
  @media only screen and (max-width: 768px){
    .cosmetic-card{
      position: relative;
      width: 150px;
      height: 250px;
      border-style: solid;
      overflow-y: hidden;
      overflow-x: hidden;
    }
  
    .img-body{
      width: 150px;
      height: 150px;
      overflow-y: hidden;
    }
  
    img{
      font-size: 16px;
    }
  
    .cosmetic-title{
      font-size: 15px;
      font-weight: bold;
      margin: 10px 0;
    }
  }
  
  /* tablet */
  @media only screen and (max-width: 1080px){
    .cosmetic-card{
      position: relative;
      width: 200px;
      height: 300px;
      border-style: solid;
      overflow-y: hidden;
      overflow-x: hidden;
    }
  
    .img-body{
      width: 200px;
      height: 200px;
      overflow-y: hidden;
    }
  
    img{
      font-size: 16px;
    }
  
    .cosmetic-title{
      font-size: 17px;
      font-weight: bold;
      margin: 10px 0;
    }
  }
.container {
    margin: 40px auto;
    display: flex;
    flex-direction: column;
}

input{
    font-size: 15px;
}
  
.cosmetic-grid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-row-gap: 25px;
    grid-column-gap: 20px;
}

.cosmetic-search {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}

.cosmetic-text {
    margin-bottom: 32px;
    text-align: center;
}

.cosmetic-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(
        -225deg,
        #ac32e4 0%,
        #7918f2 48%,
        #4801ff 100%
    );
    color: #e2e2e2;
}

.cosmetic-button {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image: linear-gradient(
        -225deg,
        #ac32e4 0%,
        #7918f2 48%,
        #4801ff 100%
    );

    color: #e2e2e2;
}

.cosmetic-input::-webkit-input-placeholder {
    color: #e2e2e2;
}

.cosmetic-input:-ms-input-placeholder {
    color: #e2e2e2;
}

.cosmetic-input::placeholder {
    color: #e2e2e2;
}


/* mobile */
@media only screen and (max-width: 768px){
    .cosmetic-grid{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 25px;
        grid-column-gap: 20px;
    }
}

/* tablet */
@media only screen and (max-width: 1080px){
    .cosmetic-grid{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-row-gap: 30px;
        grid-column-gap: 25px;
    }
}
.app{
  max-width: 100vw;
  max-height: 100vh;
}

